/* Скидання базових стилів */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Arial', sans-serif;
    background-color: #141414;
    color: #fff;
    line-height: 1.5;
  }
  
  /* Посилання */
  a {
    text-decoration: none;
    color: inherit;
  }
  
  /* Контейнер для вмісту */
  .container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Заголовки */
  h1, h2, h3, h4 {
    font-weight: 600;
  }
  
  /* Стилі хедера */
  header {
    background-color: #141414;
    border-bottom: 1px solid #333;
    padding: 10px 0;
  }
  
  .nav-menu {
    list-style: none;
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: flex-start;
  }
  
  .nav-menu li a {
    font-weight: bold;
    transition: color 0.2s ease;
  }
  
  .nav-menu li a:hover {
    color: #00b4e4; /* бірюзовий відтінок */
  }
  
  /* Стилі для Hero */
  .hero {
    position: relative;
    height: 400px;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: flex-end;
    padding: 20px;
  }
  
  .hero-overlay {
    background: linear-gradient(to bottom, rgba(20,20,20,0) 0%, rgba(20,20,20,0.8) 100%);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  
  .hero-content {
    position: relative;
    z-index: 1;
    max-width: 600px;
    margin-bottom: 20px;
  }
  
  .hero-content h1 {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .hero-content p {
    font-size: 1rem;
    line-height: 1.4;
  }
  
  /* Стилі для сітки фільмів */
  .movie-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 20px;
    margin-top: 20px;
  }
  
  /* Стилі для картки фільму */
.movie-card {
  position: relative;
  background-color: #1e1e1e;
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
  transition: transform 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-card img {
  width: 100%;
  display: block;
  object-fit: cover; /* Сохраняет пропорции изображения */
  height: auto; /* Автоматическая высота */
  border-radius: 8px 8px 0 0; /* Скругляет только верхние углы */
}

.movie-card-info {
  padding: 10px;
}

.movie-card-info h3 {
  margin: 10px 0 5px;
  font-size: 1rem;
  font-weight: 600;
}

/* Стилі для сторінки з деталями фільму */
.movie-details {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: flex-start;
}

.movie-details img {
  max-width: 300px;
  object-fit: contain; /* Зберігає пропорції зображення */
  border-radius: 8px;
  flex-shrink: 0;
}

.movie-details-content {
  max-width: 600px;
}

.movie-details-content h1 {
  margin-bottom: 10px;
}

.movie-details-content p {
  margin-bottom: 10px;
}

.movie-actors {
  width: 100%; /* Список актеров занимает всю ширину */
  margin-top: 20px; /* Отступ сверху */
}

.movie-actors h2 {
  margin-bottom: 10px;
  font-size: 1.5rem;
}

.movie-actors ul {
  display: flex; /* Располагает актеров горизонтально */
  flex-wrap: wrap; /* Переносит строки, если не хватает места */
  gap: 20px; /* Расстояние между элементами */
  list-style: none; /* Убирает маркеры списка */
  padding: 0;
  margin: 0;
  justify-content: center; /* Центрирует список */
}

.movie-actors li {
  text-align: center; /* Центрирует текст под изображением */
  max-width: 120px; /* Ограничивает ширину каждого элемента */
}

.movie-actors li img {
  width: 100%; /* Изображение занимает всю ширину контейнера */
  height: auto; /* Сохраняет пропорции изображения */
  border-radius: 50%; /* Делает изображение круглым */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /* Добавляет тень */
}

.movie-actors li p {
  margin-top: 5px;
  font-size: 0.9rem;
  color: #fff;
}
  
  /* Стилі футера */
  footer {
    background-color: #141414;
    border-top: 1px solid #333;
    padding: 20px 0;
    text-align: center;
    color: #aaa;
    margin-top: 40px;
  }
  
  footer a {
    color: #00b4e4;
    text-decoration: none;
  }
  
  /* Адаптив */
  @media (max-width: 768px) {
    .hero {
      height: 250px;
    }
    .hero-content h1 {
      font-size: 1.5rem;
    }
    .movie-details {
      flex-direction: column;
      align-items: center;
    }
  }